import request from '../utils/request'
import apiPath from './url'
import qs from 'qs'
import store from '@/store/index'

export function getList (data) {
  const url = store.getters.status ? apiPath.information.getList : apiPath.information.getListWithoutToken
  data = qs.stringify(data)
  return request({
    url: url,
    method: 'post',
    data: data
  })
}
export function getDetail (data) {
  const url = store.getters.status ? apiPath.information.getDetail : apiPath.information.getDetailWithoutToken
  data = qs.stringify(data)
  return request({
    url: url,
    method: 'post',
    data: data
  })
}
export function getDetailStatus (data) {
  // const url = store.getters.status ? apiPath.information.getDetail : apiPath.information.getDetailWithoutToken
  data = qs.stringify(data)
  return request({
    url: apiPath.information.getDetailStatus,
    method: 'post',
    data: data
  })
}
