<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>
                项目详情
            </p>
        </section>
        <section class="middle" style="overflow: hidden">
            <p class="article-title" style="float: left;width: 100%" v-html="detail.title"></p>
            <p class="collect" style="float: left;height: 30px;margin-top: -35px">
                <span v-if="detail.isCollection" @click="recollect"><img width="15px" src="../../../assets/technology/icon_Collected@2x.png"><span style="font-size: 14px">已收藏</span></span>
                <span v-else @click="collect"><img width="15px" src="../../../assets/technology/icon_Uncollected@2x.png"><span style="font-size: 14px">收藏</span></span>
            </p>
            <p class="time" style="float: right;width: 80%">
                <span><img width="12px" src="../../../assets/news/details_time_icon@2x.png" alt="">{{ detail.createTime }}</span>
            </p>
        </section>
        <hr>
        <section class="bottom">
            <article v-html="detail.article"></article>
            <div class="figure">
                <img :src="detail.coverPicture" alt="">
            </div>
            <article v-html="detail.summary"></article>
        </section>
        <div v-if="!isShow" class="button" @click="docking">
            <img src="@/assets/home/icon_xuqiuduijie@2x.png" alt="">
            我要申报
        </div>
        <div v-if="isShow" class="button" @click="showMaskClosable = true" style="color: #666666FF">
            项目申报已提交
        </div>
        <md-dialog
                :closable="true"
                v-model="iconDialog.open"
                :btns="iconDialog.btns"
        >
            <img src="../../../assets/scienceBank/toPC.png" alt="" style="margin-bottom: 20px">
            <p style="text-align: center">请您到<span style="color: #021955FF">电脑端</span>上传项目申报书!</p>
        </md-dialog>
    </main>
</template>
<script>
import { getDetail, getDetailStatus  } from '@/apiUrl/information'
import { collectP, recollect } from '@/apiUrl/technology'
import { Icon, Toast, Dialog } from 'mand-mobile'
export default {
  name: 'news_detail',
  components: {
    [Icon.name]: Icon,
    [Toast.name]: Toast,
    [Dialog.name]: Dialog
  },
  data () {
    return {
      currentId: '',
      detail: {},
        isShow: false,
      iconDialog: {
        open: false
      },
      Height: '900px'
    }
  },
  mounted () {
    this.Height = window.innerHeight - 88 + 'px'
    this.currentId = this.$route.query.id
    this.getDetail()
      this.getDetailStatus()
  },
  methods: {
    getDetail () {
      getDetail({ id: this.currentId }).then(res => {
        this.detail = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
    getDetailStatus () {
        getDetailStatus({ id: this.currentId, type: 2 }).then(res => {
          if (res.data2 === 2) {
              this.isShow = true
          } else {
              this.isShow = false
          }
      }).catch(err => {
          Toast.failed(err)
      })
    },
    // 取消收藏
    recollect () {
      const args = {
        collectionId: this.currentId,
        type: 2
      }
      recollect(args).then(res => {
        Toast.succeed('收藏成功')
        this.getDetail()
        this.collected = true
      }).catch(err => {
        Toast.failed(err)
      })
    },
    // 收藏
    collect () {
      const args = {
        id: this.currentId,
        type: 2
      }
      collectP(args).then(res => {
        Toast.succeed('收藏成功')
        this.getDetail()
        this.collected = true
      }).catch(err => {
        Toast.failed(err)
      })
    },
    docking () {
        this.$router.push({ path: '/submission/project', query: { id: this.currentId }})
    },
    // 回退
    goBack () {
      this.$router.go(-1)
    }
  }
}
</script>

<style lang="stylus" scoped>
    .app-main
        margin-bottom 0!important
        padding-top 88px
        .middle
            width 560px
            margin 45px auto
            .article-title
                font-size 32px
                color #333333
                margin-bottom 34px
            .time
                width 100%
                margin 0 auto
                display flex
                justify-content center
                font-size 24px
                span
                    display flex
                    align-items center
                    img
                        margin-right 10px
        hr
            border none
            border-bottom 1px solid #EEEEEE
            margin 30px auto
        .bottom
            overflow hidden
            padding-bottom 150px
            article
                width 560px
                margin 0 auto
                color #666666
                overflow hidden
                font-size 24px
                line-height 36px
                text-align left
                text-indent 40px
            .figure
                margin 30px auto 0
                img
                    width 560px
        .button
            position fixed
            bottom 0
            display flex
            align-items center
            justify-content center
            width 100%
            height 98px
            background rgba(229,232,238,1);
            font-size 32px
            box-shadow 0px 2px 12px 0px rgba(2,25,85,0.36);
            img
                width 26px
                height 42px
                margin-right 19px
    /deep/.md-dialog-content
        display flex
        flex-direction column
        justify-content center
        align-items center
</style>
